.btn{
    padding: 0.25em 1em;
    text-align: center;
    font-family: $bodytext;
    font-size: 1.25rem;
    font-weight: 300;
    line-height: 1.7;
    border-radius: 0.25rem;
    text-decoration: none;
    color: $secondcol;
    border: 1px solid $secondcol;
    text-decoration: none;
    display: inline-block;
    cursor: pointer;
    &:hover{
        opacity: 0.9;
        text-decoration: none;
    }
    &:active{
        opacity:0.9;
    }
    &:focus{ /*Input Focus Settings*/
      outline: none;
      box-shadow: 0 0 2pt 1pt $focuscolor;
      transition: box-shadow 250ms ease;
      text-decoration: none;
    }
    
}


.herobtn{
  display: inline-block;
  background-color: #0a0b04;
  color:$clientsecondary;
  border: 1px solid $clientprimary;
  text-transform: uppercase;
  font-size: 1.4rem;
  padding: 0.5rem 1rem;
  &:focus{
    box-shadow: 0 0 2pt 1pt rgb(243, 231, 231);
  }
}

.featurecardbtn button{
  
  box-shadow: 0 1px 1px rgba(0,0,0,0.15), 
              0 2px 2px rgba(0,0,0,0.15), 
              0 4px 4px rgba(0,0,0,0.15), 
              0 8px 8px rgba(0,0,0,0.15);
              transition: box-shadow 150ms ease-in-out;
  &:hover,&:focus{
   
    box-shadow: 0 0 0;
    transition: box-shadow 150ms ease-in-out;
  }
}

.btn-primary,
.primary-muted{
  padding: 0.75rem;
  border: 1px solid $primarycol;
  border-radius: 0.25rem;
  background-color: $primarycol;
  color: #fff;
  text-transform: uppercase;
  &:hover, &:focus{
    text-decoration-line: none;
  }
}

.primary-muted{
  background-color: #fff;
  color: $primarycol;
  border-color: $secondcol;
}